import React from 'react'
import img from '../../sources/img/welcome.png'
function Welcome() {
    return (
        <div className="container">
            <div >
                <h2 className="text-center mt-4">
                    Bemvind@ {localStorage.getItem('username')}
                </h2>
                <img src={img} width="400" className="d-block mx-auto"></img>
               <p className="text-center h5">
               Neste site você pode conferir informaçoes sobre o novo Coronavirus ,assim como conhecer outras pessoas 
                   </p>

                <a href="#/app/home" className="btn btn-outline-danger btn-lg btn-block">
                    Inicio
                </a>
            </div>
            
        </div>
    )
}

export default Welcome
