import React from "react";
import './Navigation.css'

function Navigation() {


const exit =()=>{
  localStorage.removeItem('email')
  localStorage.removeItem('username')
  localStorage.removeItem('accesToken')
  localStorage.removeItem('imgProfile')

  window.location.href = "#/"
}

  return (
      
    <nav className="navbar navbar-expand-lg navbar-dark rigth  bg-dark sticky-top">
  <a className="navbar-brand" href="/App">CoviDWeb-19</a>
  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
    <span className="navbar-toggler-icon"></span>
  </button>
  <div className="collapse navbar-collapse " id="navbarNavAltMarkup">
    <div className="navbar-nav  ml-auto">
      <a className="nav-link active" href="#/App/Home">Inicio<span className="sr-only">(current)</span></a>
      <a className="nav-link active" href="#/App/WorldInfo">Dados<span className="sr-only">(current)</span></a>
      <a className="nav-link active" href="#/App/Notices">Noticias <span className="sr-only">(current)</span></a>

      <a className="nav-link active" href="#/App/Profile">Perfil<span className="sr-only">(current)</span></a>
      <a className="nav-link active" href="#/App/Credit">Notificações <span className="sr-only">(current)</span> <span class="badge badge-light">4</span></a>
      <a className="nav-link active rigth btn" onClick={()=>exit()}>Sair <span className="sr-only">(current)</span></a>

    </div>
  </div>
  </nav>
  );
}

export default Navigation;
