import React from 'react'
import './Profile.css'
function Profile() {
    return (
      
<div class="row">


<div class="col-lg-5">
    <div class="media">
        <a class="pull-left" href="/">
            <img class="media-object" src={localStorage.getItem('imgProfile')} alt="profile" style={{width: '100px',height: '100px'}}/>
        </a>
        <div class="media-body">
            <h4 class="media-heading">{localStorage.getItem('username')}</h4>
    <h5>{localStorage.getItem('email')}</h5>
            <hr style={{margin: '9em'}}/>

        
        </div>
    </div>

</div>



</div>
  
    )
}

export default Profile
