import React,{useEffect} from 'react'
import './Login.css'
import { GoogleLogin } from 'react-google-login';


function Login({login}) {


useEffect(() => {
	if(localStorage.getItem('accesToken')){
		window.location.href="#/App/Welcome"
	}
	
}, [])

	const responseGoogle = (response) => {
			 console.log(response);
			if(response.profileObj){
				localStorage.setItem('username',response.profileObj.givenName + " " + response.profileObj.familyName)
				localStorage.setItem('email',response.profileObj.email)
				localStorage.setItem('accesToken',response.accessToken)
				localStorage.setItem('imgProfile',response.profileObj.imageUrl)
				
				window.location.href = "#/app/welcome"

				


			}
	  }

    return (
<div className="login-wrap">
	<div className="login-html">
		<input id="tab-1" type="radio" name="tab" className="sign-in" checked/><label style={{color:'#ffff'}} for="tab-1" className="tab">Sign In</label>
		<input id="tab-2" type="radio" name="tab"  className="sign-up"/><label style={{color:'#ffff'}} for="tab-2" className="tab">Sign Up</label>
		<div className="login-form">
			<div className="sign-in-htm">
				<div className="group">
					<label for="user" className="label">Username</label>
					<input id="user" type="text" className="input"/>
				</div>
				<div className="group">
					<label for="pass" className="label">Password</label>
					<input id="pass" type="password" className="input" data-type="password"/>
				</div>
			
				<div className="group">
					<input type="submit" className="button" value="Sign In" onClick={()=>login()}/>
				
					<GoogleLogin className="d-block mx-auto mt-4 log"
     clientId="365928965363-tiqenhoq6ifss2r9jighm1d0lhvinnlr.apps.googleusercontent.com"
	 onSuccess={responseGoogle}
	 onFailure={responseGoogle}
	 cookiePolicy={'single_host_origin'}
  />
  
				</div>
				<div className="foot-lnk">
					<a href="#forgot">Forgot Password?</a>
				</div>
			</div>
			<div className="sign-up-htm">
				<div className="group">
					<label for="user" className="label">Username</label>
					<input id="user" type="text" className="input"/>
				</div>
				<div className="group">
					<label for="pass" className="label">Password</label>
					<input id="pass" type="password" className="input" data-type="password"/>
				</div>
				<div className="group">
					<label for="pass" className="label">Repeat Password</label>
					<input id="pass" type="password" className="input" data-type="password"/>
				</div>
				<div className="group">
					<label for="pass" className="label">Email Address</label>
					<input id="pass" type="text" className="input"/>
				</div>
				<div className="group">
					<input type="submit" className="button" value="Sign Up"/>
				</div>
				<div className="hr"></div>

			</div>
		</div>
	</div>
</div>
    )
}

export default Login
